@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0;
  font-family: 'Work Sans';

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.PokeCard {
    text-align: center;
    width: 300px;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
    background-color: white;
    border-radius: 3px;

}

.PokeCard-data {
    font-size: 80%;
}

.PokeCard-title {
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: 1.5rem;
}

.PokeCard-image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    background-color: #eceff1;
    border-radius: 3px;
    margin-bottom: 1rem;
}

.PokeCard img {
    transition: all ease 500ms;
}

.PokeCard img:hover {
    transform: scale(1.1);


}
.PokeDex {
    text-align: center;
}

.PokeDex-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;

}

.PokeDex-looser {
    color: red;
}

.PokeDex-winner {
    color: green;
}
